body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.navbar {
  background: rgb(129, 18, 129);
  min-height: 50px;
  
  width: 100%;
  font-size: calc(4.4px + 1.75vw);
  
  white-space: nowrap;
  display: block;
   
}

li {
  
  width: 33.3%;
  padding: 26px 0;
  text-align: center;
  color: white;
  
  list-style-type: none;
  display: inline-block;

 
  
}

a {
  color: white;
}



.header {
  min-height: 200px;
  width: 100%;
  
  text-align: center;
  background-image: url("/images/17226256-pink-flowery-background.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  color: purple;
  display: block;
  
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 20%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;

}



